"use client";
import { useRouter } from "next/navigation";
import Cookies from "js-cookie";

export default function Header({ title, buttonLabel, onButtonClick, backurl }) {
  const router = useRouter();

  // Fun√ß√£o para fazer logout
  const handleLogout = () => {
    Cookies.remove("token");
    router.push("/"); // Redireciona para a tela de login
  };

  return (
    <div className="bg-gray-800 text-white p-6 shadow-lg rounded-lg flex items-center justify-between">
      {/* Bot√£o de voltar */}
      
      <button
        onClick={() => router.push(backurl)}
        className="bg-red-500 px-4 py-2 rounded-lg text-lg font-semibold hover:bg-red-600 transition shadow-md"
      >
        ‚Üê
      </button>
      <button
          onClick={handleLogout}
          className="bg-gray-600 px-6 py-2 ml-4 text-lg rounded-lg font-semibold hover:bg-gray-700 transition shadow-md"
        >
           Sair
        </button>

      {/* T√≠tulo centralizado */}
      <h1 className="text-3xl font-bold flex-1 text-center">{title}</h1>

      {/* Bot√µes de a√ß√£o */}
      <div className="flex gap-4">
        {buttonLabel && (
          <button
            onClick={onButtonClick}
            className="bg-green-500 px-6 py-2 text-lg rounded-lg font-semibold hover:bg-green-600 transition shadow-md"
          >
            {buttonLabel}
          </button>
        )}

        {/* Bot√£o de Logout */}
        
      </div>
    </div>
  );
}
